            lesson.scoreMax - lesson.score, 
            lesson.scoreTotal - lesson.scoreMax]" 
            :colors="[mix3(100),mix3(0),'#00000015']"
            :thickness='4'>
              <i v-if="lesson.completed" :style='{color: $green}' class="material-icons">done</i>
              <i v-if="lesson.started && !lesson.completed" style='color:#0004' class="material-icons">schedule</i>
              <i v-if="!lesson.started" style='color:#0002' class="material-icons-outlined">play_circle</i>
          </chart-doughnut>
          <div v-if="lesson.completed" class="lesson-percentage" :style='{background: tagBackground}'>{{ lesson.percentage/10 }}</div>
      </div>
    </div>

    <div>
      <div>
